<template>
  <div class="box">
    <Header>
      <div slot="center">支付</div>
    </Header>
    <div class="content">
      <div class="price">
        <p>需支付<span>￥{{ totalPrice }}</span></p>
      </div>
      <p class="market">
        <i class="iconfont icon-zhifu"></i>商超支付
      </p>
      <ul class="card">
        <li>
          <i class="iconfont icon-yinhangqiazhifuzhangdanfukuanjinqianchucunqia cart"></i>
          <div class="panel">
            <p>银行卡</p>
            <span>储蓄卡</span>
          </div>
          <div class="checked">
            <van-radio-group v-model="radio">
              <van-radio name="card" checked-color="#07c160"></van-radio>
            </van-radio-group>
          </div>
        </li>
        <li>
          <i class="iconfont icon-umidd17 cart"></i>
          <div class="panel">
            <p>支付宝支付</p>
            <span>仅安装支付宝6.0.2 及以上版本客户端使用</span>
          </div>
          <div class="checked">
           <van-radio-group v-model="radio">
              <van-radio name="airpay" checked-color="#07c160" ></van-radio>
            </van-radio-group>
          </div>
        </li>
        <li>
          <i class="iconfont icon-weixinzhifu cart"></i>
          <div class="panel">
            <p>微信支付</p>
            <span>仅安装微信6.0.2 及以上版本客户端使用</span>
          </div>
          <div class="checked">
           <van-radio-group v-model="radio">
              <van-radio name="vx" checked-color="#07c160"></van-radio>
           </van-radio-group>
          </div>
        </li>
      </ul>
      <div class="password">
        <van-cell-group>
          <van-field
            v-model="password"
            type="password"
            label="密码"
            placeholder="请输入密码"
            required
            @focus="passfocus"
          />
        </van-cell-group>
        <van-number-keyboard
          :show="show"
          theme="custom"
          extra-key="."
          close-button-text="完成"
          @blur="show = false"
          v-model="password"
        />
      </div>
    </div>
    <footer class="footer">
      <i @click='showtip'>组合支付￥{{this.totalPrice}}</i>
    </footer>
  </div>
</template>

<script>
import Header from '@/components/Header'
import axios from '@/utils/request'
import Vue from 'vue'
import { Panel, NumberKeyboard, Field, CellGroup, RadioGroup, Radio, Dialog } from 'vant'
Vue.use(Panel)
Vue.use(NumberKeyboard)
Vue.use(Field)
Vue.use(CellGroup)
Vue.use(RadioGroup)
Vue.use(Radio)
export default {
  components: {
    Header,
    [Dialog.Component.name]: Dialog.Component
  },
  data () {
    return {
      radio: 'card',
      show: false,
      password: '',
      mag: '',
      totalPrice: ''
    }
  },
  methods: {
    passfocus () {
      this.show = true
      // console.log(this.password)
    },
    showtip () {
      // console.log(this.password * 1 === this.totalPrice * 1)
      if (this.password * 1 === this.totalPrice * 1) {
        axios.get('order/updatestatus?orderid=' + this.$route.query.orderid + '&status=' + 1).then(res => {
        })
        Dialog({ message: '支付成功', showConfirmButton: true })
        this.$router.replace('/user')
      } else {
        Dialog({ message: '密码输入不正确', showConfirmButton: true })
      }
    }
  },
  created () {
    let b = this.$route.query.totalPrice
    this.totalPrice = b / 100
    // console.log(this.totalPrice)
    axios.get('order/updatestatus?orderid=' + this.$route.query.orderid + '&status=' + 0).then(res => {
      console.log('成功1')
    })
  }
}
</script>

<style lang="scss" >
@import '@/lib/reset.scss';
.box{
  @include rect(100%,100%);
  .content{
    @include rect(100%,100%); @include flexbox();@include flex-direction(column);@include flex();
    .price{@include rect(100%,.5rem);@include background-color(#fff);@include border();
      p{text-align: right;font: .17rem/.5rem '';margin-right: .12rem;@include color(#232326);
        span{@include color(#f23030);}
      }
    }
    .market{@include rect(100%,.5rem);@include color(#f23030);font:.19rem/.5rem '';margin:.05rem 0 .05rem .2rem;
      i{font-size:.3rem;@include display(inline-block);margin-right:.1rem;}
    }
    .card{
      @include rect(100%,auto);
      li{
        @include flexbox();@include background-color(#fff);@include rect(100%,.7rem);
        justify-content:flex-start;position: relative;align-items:center;margin-bottom:.1rem;
        .cart{font-size:.3rem;margin:10px 20px 0 10px;}
        .icon-umidd17 {color:#1296db;}
        .icon-weixinzhifu{color:rgb(7, 193, 96);}
        .panel{
          p{font:.16rem/.35rem "";}
          span{font:.12rem/.1rem "";@include color(#848484);}
        }
        .checked{position: absolute;right:.2rem;bottom:.25rem;}
      }
    }
    .password{
      @include rect(100%,auto);margin-bottom: .1rem;
    }
  }
  .footer{
    text-align: center;
    i{
      @include background-color(#f23030);@include display(block);
      @include rect(100%,100%);font:.16rem/.5rem ''; color:#fff;
    }
  }
}
</style>
